.page {
  height: 100%;
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 17px;
  line-height: 20px;
}

.page__body {
  min-height: 100%;
  margin: 0 auto;
  padding: 0;
  background-color: $basic-white;
}

.page__container {
  width: 1230px;
  margin: 0 auto;

  @media (max-width: $tablet-width-only) {
    width: $tablet-width;
  }

  @media (max-width: $mobile-width-only) {
    width: $mobile-width;
  }
}

.page__container--header {
  width: 880px;
  margin: 0 auto;

  @media (max-width: $tablet-width-only) {
    width: 100%;
  }
}
